          flex-wrap: wrap;
}

.search-list-container {
  margin: 0 40px 0 40px;
}

.title {
  width: 100%;
}
.title h1 {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 4rem;
