.container {
  position: relative;
  width: 12.5rem;
  margin-left: 0.75rem;
  border-radius: 6px;
  padding: 0.5rem;
  background: rgba(27, 27, 110, 0.185);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border: 0.5px solid rgba(70, 70, 70, 0.459);
  box-shadow: rgba(32, 32, 32, 0.459) 1px 1px 3px 1px;
  cursor: default;
}

h4 {
  background: transparent;
  margin-right: 0.35rem;
}
.price {
  color: rgb(235, 226, 253);
}
span {
  color: #5306fc;
  background: transparent;
}
img {
  width: 1rem;
  height: 1rem;
  background-color: transparent;
  margin-right: 0.2rem;
}
.fa-times {
  position: absolute;
  background: transparent;
  font-size: 1.3rem;
  cursor: pointer;
  right: 0.55rem;
}

@media (max-width: 800px) {
  .container {
    width: 11rem;
    padding: 0.3rem;
    margin-left: 0.5rem;
  }
  h4 {
    font-size: 0.9rem;
  }
  img {
    width: 0.9rem;
    height: 0.9rem;
  }
  .fa-times {
    font-size: 1.2rem;
  }
}

.coin-search {
  margin-bottom: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.coin-text {
  font-size: 2rem;
  margin-bottom: 10px;
  text-align: center;
}
.search-form {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.selected-coin-in-search-container {
  position: absolute;
  background-color: transparent;
}
.coin-input {
  padding-left: 16px;
  width: 320px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
  font-size: 1rem;
}
::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.658);
}
:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.658);
}
::placeholder {
  color: rgba(255, 255, 255, 0.658);
}
@media (max-width: 800px) {
  .coin-text {
    font-size: 1.75rem;
  }
  .coin-input {
    width: 250px;
    height: 40px;
  }
}

@media (max-width: 555px) {
  .coin-text {
    font-size: 1.5rem;
  }
  .coin-input {
    width: 250px;
    height: 40px;
  }
}

.coin-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.coin-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 60px;
  width: 100%;
  padding: 0 1rem 0 1rem;
  background-color: rgb(41, 43, 66);
  border-bottom: solid 1px gray;
  cursor: pointer;
}
.selected-dropdown {
  background-color: rgb(62, 65, 99);
}

.coin {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  background-color: transparent;
}
.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
  background-color: transparent;
}

.coin h1 {
  font-size: 1rem;
  width: 150px;
  background-color: transparent;
}
.coin-symbol {
  text-transform: uppercase;
  background-color: transparent;
}
.coin-data {
  display: -webkit-flex;
  display: flex;
  text-align: center;
}
.coin-price {
  width: 110px;
}
.coin-marketcap {
  width: 155px;
}
@media (max-width: 800px) {
  .coin-row {
    padding-right: 1.5rem;
  }
  .coin h1 {
    font-size: 0.9rem;
    width: 100px;
    margin-right: 15px;
  }
  .coin-symbol {
    font-size: 0.9rem;
  }
}

.coins-container {
  min-height: 80px;
  max-height: 240px;
  overflow: scroll;
  border: solid 0.5px gray;
  overflow-x: hidden;
  margin-bottom: 20px;
  width: 320px;
  position: absolute;
  z-index: 1;
}
.coins-container::-webkit-scrollbar {
  width: 10px;
}
.coins-container::-webkit-scrollbar-track {
  background: rgb(51, 53, 85);
}
::-webkit-scrollbar-thumb {
  background: #7717f3;
}
p {
  text-align: center;
}
.icon-div {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  text-align: center;
  opacity: 50%;
}
@media (max-width: 800px) {
  .coins-container {
    width: 250px;
  }
}

@-webkit-keyframes anim {
  from {
    opacity: 0;
    margin-top: 10rem;
  }
  to {
    margin-top: 5rem;
    opacity: 1;
  }
}
@keyframes anim {
  from {
    opacity: 0;
    margin-top: 10rem;
  }
  to {
    margin-top: 5rem;
    opacity: 1;
  }
}
.selectedCoin-container-parent {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.selectedCoins-container {
  margin: 5rem 1rem 0 1rem;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  opacity: 1;
  -webkit-animation-name: anim;
          animation-name: anim;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
.market-cap-title {
  grid-column: 1/5;
  grid-row: 1/2;
  text-align: center;
  margin-bottom: 0.5rem;
}
.a-img {
  width: 4rem;
  height: 4rem;
}
.b-img {
  width: 1.5rem;
  height: 1.5rem;
  -webkit-align-self: center;
          align-self: center;
  margin-right: 0.25rem;
}
.first-text-img {
  grid-column: 1/2;
  grid-row: 2/3;
}
.second-text-img {
  grid-column: 1/2;
  grid-row: 3/4;
}
.text-div {
  display: grid;
  grid-template-columns: auto auto 0.5rem auto;
  grid-template-rows: auto auto auto;
  margin-top: 3.5rem;
}
.first-text {
  grid-column: 2/3;
  grid-row: 2/3;
  -webkit-align-items: center;
          align-items: center;
}
.second-text {
  grid-column: 2/3;
  grid-row: 3/4;
  -webkit-align-items: center;
          align-items: center;
}
.first-mcap {
  grid-column: 4/5;
}
.second-mcap {
  grid-column: 4/5;
}

.coin-prices {
  font-size: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.img-price {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.percent {
  margin-left: 0.5rem;
  font-weight: lighter;
  font-size: 1.5rem;
}
.positive {
  color: #16c784;
}
.negative {
  color: #ea3943;
}
@media (max-width: 800px) {
  .coin-prices {
    font-size: 2.5rem;
  }
}

@media (max-width: 555px) {
  .selectedCoins-container {
    margin: 2rem 1rem 0 1rem;
  }
  .coin-prices {
    font-size: 2rem;
  }
  .percent {
    font-size: 1.25rem;
  }

  .a-img {
    width: 3.25rem;
    height: 3.25rem;
  }
  .b-img {
    width: 1rem;
    height: 1rem;
  }
  .text-div h2 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1rem;
  }
  .text-div {
    margin-top: 2.5rem;
  }
  @-webkit-keyframes anim {
    from {
      opacity: 0;
      margin-top: 5rem;
    }
    to {
      margin-top: 2rem;
      opacity: 1;
    }
  }
  @keyframes anim {
    from {
      opacity: 0;
      margin-top: 5rem;
    }
    to {
      margin-top: 2rem;
      opacity: 1;
    }
  }
}
@media (max-width: 360px) {
  .text-div h2 {
    font-size: 1.25rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  background-color: rgb(26, 27, 43);
  color: white;
